.experience-card-body-div {
  width: 100%;
}

.experience-card-header-div {
  display: flex;
  flex-direction: row;
}

.experience-card-logo {
  width: 70px;
  -webkit-box-shadow: 0px 13px 25px -2px #7F8DAA;
  -moz-box-shadow: 0px 13px 25px -2px #7F8DAA;
  box-shadow: 0px 13px 25px -2px #7F8DAA;
  border-radius: 50px;
}

.experience-card-title {
  /* font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif; */
  margin-top: 10px;
  /* margin-bottom: 2px; */
  /* margin-left: 0px; */
  /* margin-right: 7px; */
  text-align: left;
}

.experience-card-company {
  /* font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif; */
  margin: 0;
  text-align: left;
}

.experience-card-duration {
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  /* margin-top: 10px; */
  margin-bottom: 2px;
  margin-left: 0px;
  margin-right: 0px;
  text-align: right;
  color: #868e96;
}

.experience-card-heading-left {
  float: left;
  margin-left: 10px;
  margin-right: auto;
}

.experience-card-heading-right {
  float: right;
  margin-left: auto;
  margin-right: 10px;
}

.experience-card-location {
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  margin: 0;
  text-align: right;
  color: #868e96;
}

.experience-card-description {
  margin-top: 5px;
  margin-left: 10px;
  text-align: justify;
  margin-right: 20px;
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
}


.experience-card-company>a {
  position: relative;
  color: #000;
  text-decoration: none;
}

.experience-card-company>a:hover {
  color: #00000000;
}

.experience-card-company>a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.experience-card-company>a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  /* background-color: #000; */
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.experience-list-item {
  display: flex;
  align-items: center;
  flex-direction: row;
}

