@keyframes ripple {
  0% {
    left: 5px;
    top: 5px;
    opcity: 75;
    width: 0;
    height: 0;
  }

  100% {
    left: -20px;
    top: -20px;
    opacity: 0;
    width: 50px;
    height: 50px;
  }
}

@media (max-width: 768px) {
  .experience-card {
    /* margin-bottom:10px; */
    flex-direction: column;
    text-align: center;
    /* margin-left: 0px; */
  }

  .experience-list-item {
    flex-direction: column;
  }

  .experience-card-logo {
    width: 100px;
  }

  .experience-card-heading-left {
    float: center;
    margin-left: auto;
    margin-right: auto;
  }

  .experience-card-heading-right {
    float: center;
    margin-left: auto;
    margin-right: auto;
  }

  .experience-card-title {
    /* text-align: center; */
  }

  .experience-card-company {
    /* text-align: center; */
  }

  .experience-card-duration {
    /* margin-top: 2px; */
    /* text-align: center; */
  }

  .experience-card-location {
    /* text-align: left; */
    /* text-align: center; */
  }

  .experience-card-header-div {
    flex-direction: column;
  }

  .experience-card-description {
    margin-left: 14px;
    margin-right: 14px;
    text-align: justify;
  }

  .experience-card-header {
    flex-direction: column !important;
  }

  .experience-card-heading-right {
    /* text-align: left; */
    float: left;
  }

  .experience-card-duration {
    text-align: left !important;
  }
}

.minimal {
  font-size: 15px;
}

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}

.text-align-justify {
  text-align: justify;
}

.custom-card-body {
  border-radius: 8px;
}

.experience-text-gradient {
  background: linear-gradient(150deg, #3b79b3 21.62%, #194a79 50%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  line-height: 1.2;
}